                                                        <Col span="6">异常通知方式：{{}}</Col>
                                                        <Col span="14">异常通知内容：{{}}</Col>
                                                    </Row>
                                                </div>
                                                <div class="monitor-notice">
                                                    <p>
                                                        <span>异常通知企业：</span>
                                                    </p>
