<script>
import AdtSchemeInformationRoute from '@/components/adt-scheme-information-route/Index'
import AdtTaskContent from '@/components/adt-task-content/Index'
import { apiGetSchemeDetails, apiGetSceneQueryList } from '@/api/logistics'
export default {
    components: {
        AdtTaskContent,
        AdtSchemeInformationRoute
    },
    props: {
        logisticsData: {}
    },
    data() {
        return {
            isShow: false,
            // 代理服务
            schemeProxyList: [],
            getDetailsData: {},
            logistics: [],
            transportValue: '',
            placeValue: '',
            customsValue: '',
            bondValue: '',
            listData: {},
            distinguishCharacter: '',
            changeOneText: '展开',
            changeTwoText: '展开',
            changeThreeText: '展开',
            changeFourText: '展开',
            changeIndex: 1,
            titleNames: ['运输服务', '场所服务', '关务服务', '综保区服务'],
            // 场景数据
            scenesList: [],
            // 产品编号
            serverCode: '',
            titleNamesScenes: [],
            transportServiceProvider: [],  // 运输展示的服务商
            placeServiceProvider: [], // 场所展示的服务商
            customsServiceProvider: [], // 关务服务展示的服务商
            bondServiceProvider: [], // 综保区展示的服务商
            checkAllGroup: [],
            num: '默认值',
            changeIndexScenes: 1,
            titleNamesTask: ['任务', '监控设置', '协同协作设置'],
            changeIndexTask: 1,
            basicTask: [],
            basicTaskData: {},
            additionalTasks: []
        }
    },
    mounted() {
        // 取本地的账号角色信息 调度：dspt，销售：mkt，客户：cstm，
        // this.JSON.parse(window.sessionStorage.getItem('goodsData'))
        this.distinguishCharacter = 'dspt'
        // 获取列表中存储在本地sessionStorage的数据
        this.listData = JSON.parse(window.sessionStorage.getItem('logisticsData')) || {}
        this.logistics = this.listData.routeLists || []
        console.log(this.listData)
        this.getData ()
    },
    methods: {
        getData () {
            const data = {
                freightReqCode: this.listData.freightReqCode,
                logisticsSchemeCode: this.listData.logisticsSchemeCode,
                routeCode: this.listData.routeCode
            }
            apiGetSchemeDetails({ data: data }).then( res => {
                if (res.code == 200) {
                    this.getDetailsData = res.data
                    this.schemeProxyList = res.data.schemeProxyList || []
                    this.transportServiceProvider = res.data.serverTransportList || []
                    this.placeServiceProvider = res.data.serverStationList || []
                    this.customsServiceProvider = res.data.serverCustomsList || []
                    this.bondServiceProvider = res.data.serverBondList || []
                    // 第一次进页面展示场景及任务
                    // const scenesData = {
                    //     "serverCode": this.transportServiceProvider[0][0].serverCode,
                    //     "serviceType": this.transportServiceProvider[0][0].serviceType
                    // }
                    // apiGetSceneQueryList({ data: scenesData }).then(res => {
                    //     if (res.code == 200) {
                    //         this.scenesList = res.data || []
                    //         const arr = []
                    //         this.scenesList.forEach((item,index) => {
                    //             arr.push(`场景${index+1}`)
                    //         });
                    //         this.titleNamesScenes = arr
                    //     }
                    // })
                }
            })
        },
        // 折叠面板展开/收起切换
        changeFont (index) {
            if (index == 1) {
                if (this.changeOneText == '展开') {
                    this.changeOneText = '收起'
                } else {
                    this.changeOneText = '展开'
                }
            }
            if (index == 2) {
                if (this.changeTwoText == '展开') {
                    this.changeTwoText = '收起'
                } else {
                    this.changeTwoText = '展开'
                }
            }
            if (index == 3) {
                if (this.changeThreeText == '展开') {
                    this.changeThreeText = '收起'
                } else {
                    this.changeThreeText = '展开'
                }
            }
            if (index == 4) {
                if (this.changeFourText == '展开') {
                    this.changeFourText = '收起'
                } else {
                    this.changeFourText = '展开'
                }
            }
            // 赋值临时解决方案
            if (!this.transportValue.length) {
                this.isShow = false
            }
            if (this.transportValue[0] > 0) {
                this.isShow = false
            }
            if (!this.placeValue.length) {
                this.isShow = false
            }
            if (this.placeValue[0] > 0) {
                this.isShow = false
            }
            if (!this.customsValue.length) {
                this.isShow = false
            }
            if (this.customsValue[0] > 0) {
                this.isShow = false
            }
            if (!this.bondValue.length) {
                this.isShow = false
            }
            if (this.bondValue[0] > 0) {
                this.isShow = false
            }
            // if (this.value.length) {
            //     // console.log(123)
            //     // this.transportServiceProvider = this.getDetailsData.serverTransportList || []
            // }
        },
        handleBtn (index) {
            this.changeIndex = index + 1
        },
        handleBtnScenes (index) {
            this.changeIndexScenes = index + 1
            // 场景切换的时候重新从后台拿数据
            this.scenesDataHandle(this.scenesList)
        },
        handleBtnTask (index) {
            this.changeIndexTask = index + 1
        },
        selectCompany (id, val) {
            this.num = id
            this.serverCode = val.serverCode
            const data = {
                "serverCode": val.serverCode,
                "serviceType": val.serviceType
            }
            apiGetSceneQueryList({ data: data }).then(res => {
                if (res.code == 200) {
                    if (res.data.length) {
                        this.isShow = true
                        this.scenesList = res.data || []
                        this.scenesDataHandle(this.scenesList)
                    } else {
                        this.isShow = false
                    }
                } else {
                    this.isShow = false
                }
            })
        },
        scenesDataHandle (data) {
            const arr = []
            const scenesTransition = data[Number(this.changeIndexScenes) - 1] || {}
            data.forEach((item,index) => {
                arr.push(`场景${index+1}`)
            });
            this.titleNamesScenes = arr
            this.basicTaskData = {
                serverDuration: scenesTransition.serverDuration,
                serverDurationUnit: scenesTransition.serverDurationUnit,
                taskCost: scenesTransition.taskCost,
                curr: scenesTransition.curr,
            }
            this.basicTask = scenesTransition.taskRes.taskBaseList || []
            this.additionalTasks = scenesTransition.taskRes.taskAffixList || []
        }
    },
}
</script>

